import axios from 'axios'
import Vue from 'vue'
import router from '../router'
import { Dialog } from 'vant';

Vue.use(Dialog);

/**
 * 
 * TIME_OUTERR_OK
 */
export const TIME_OUT = 5000;    // 
export const ERR_OK = true;      // 
export const baseUrl = process.env.BASE_URL   // urlprocess.env
export const imgFileUrl = process.env.imgFileUrl  // 

// 
axios.defaults.timeout = TIME_OUT
axios.defaults.withCredentials=true;

// 
axios.interceptors.request.use(
  config => {
    let token = localStorage.getItem('token')   // token
    config.headers['Content-Type'] = 'application/json;charset=UTF-8;'//
    config.headers['Authorization'] = ''
    if(token != null){                          // tokennulltoken
      config.headers['Authorization'] = token
    }
    return config
  },
  error => {
    return Promise.reject(error)
  }
)
// token
axios.interceptors.response.use(
  response => {
    let {data} = response
    if(response.data.code=='501'&&response.data.msg==''&&response.data.success==false){
    //if (data.message === 'token failure!') {    // token
      //localStorage.removeItem('token')          // tokentoken
      // 
      Dialog.alert({
        message: ''
      }).then(() => {
        router.push('/home/login');
      });

    } else {
      return Promise.resolve(response)
    }
  },
  error => {
    return Promise.reject(error)
  }
)
// post
export function fetch(requestUrl, params = '') {
  return axios({
    url: requestUrl,
    method: 'post',
    data:  params

  })
}

